<template>
  <div class="container">
    <div class="detail">
      <h2>{{ post.post }}</h2>
      <h3>{{ post.name }}</h3>
      <h4>{{ post.date }}</h4>
    </div>
  </div>
</template>

<script>
import {csrftoken} from '../csrf/csrf_token'

export default {

  data() {
    return {
      post: {}
    }
  },

  props: {
    slug: {
      type: String,
      required: true,
    },
  },
  methods: {
    getDetailPost() {
      fetch(`/post-api/posts/${this.slug}/`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          'X-CSRFTOKEN':csrftoken
        },
      })
        .then((response) => response.json())
        .then((data) => {
          this.post = data
          console.log(data)
        })
        .catch((error) => console.log(error));
    },
  },
  created() {
    this.getDetailPost()
  }
};
</script>

<style scope>
  .detail {
    padding: 20px 10px;
    background-color: #888;

  }
</style>