<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-md-8 col-md-offset-2 main">
        <h1>Create post</h1>

        <form @submit.prevent="insertPost">
          <div class="form-group mt-3 mb-1">
            <label for="title">Name <span class="require">*</span></label>
            <input
              type="text"
              class="form-control"
              name="name"
              v-model="name"
            />
          </div>

          <div class="form-group mt-3 mb-1">
            <label for="description">Post</label>
            <textarea
              rows="5"
              class="form-control"
              name="post"
              v-model="post"
            ></textarea>
          </div>

          <div class="form-group">
            <p><span class="require">*</span> - required fields</p>
          </div>

          <div class="form-group">
            <button type="submit" class="btn btn-primary mx-3">Publish post</button>
            <button class="btn btn-danger">Cancel</button>
          </div>
        </form>

        <div v-if="error" class="alert alert-warning alert-dismissable fade show mt-5" role="alert">
            <strong>{{ error }}</strong>
        </div>

      </div>
    </div>
  </div>
</template>


<script>
import {csrftoken} from '../csrf/csrf_token'

export default {
  data() {
    return {
      name: null,
      post: null,
      error: null
    };
  },
  methods: {
    insertPost() {
      if (!this.name || !this.post) {
        this.error = "Please add the text there!";
      } else {
        fetch("post-api/posts/", {
          method: "POST",
          headers: {
            'Content-Type': "application/json",
            'X-CSRFTOKEN':csrftoken
          },
          body: JSON.stringify({name:this.name, post:this.post}),
        })
          .then((response) => response.json())
          .then(() => {
            this.$router.push({
              name: "home",
            });
          })
          .catch((error) => console.log(error));
      }
    },
  },
};
</script>


<style scope>
.require {
  color: #666;
}
label small {
  color: #999;
  font-weight: normal;
}
.main {
  margin-left: auto !important;
  margin-right: auto !important;
  text-align: left !important;
}
</style>
